.backdrop-container {
    background-color: rgba(26, 30, 37, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}

.backdrop-wrap {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1001;
}

.backdrop-item {
    background-color: #eeeeee;
    width: 100%;
    border: 2px solid #eeeeee;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
    filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}

.backdrop-item-pic-wrap {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.backdrop-item-pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #eeeeee;
    background-color: #d65a31;
    box-sizing: border-box;
}

.backdrop-image {
    position: relative;
    min-width: 10vw;
    min-height: 10vh;
    max-width: 90vw;
    max-height: 80vh;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    object-fit: cover;
    transition: all 0.2s linear;
}

.backdrop-item-info {
    padding: 20px 30px 30px;
}

.backdrop-item-text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

.backdrop-close-icon {
    background-color: #1A1E25;
    border: 2px solid #eeeeee;
    border-radius: 15px;
    width: 30px;
    height: 30px;
    color: #eeeeee;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -10px;
    margin-right: -10px;
    cursor: pointer;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 5px;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
    filter: drop-shadow(0 6px 20px rgba(0, 0, 0, 0.017));
}

.backdrop-close-icon:hover {
    background-color: #eeeeee;
    color: #1A1E25;
    font-weight: bold;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.3);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
}

@media screen and (max-width: 960px) {
    .backdrop-image {
        min-width: auto;
        min-height: auto;
    }
}
.cards {
    padding: 2rem 4rem;
    background: #eeeeee;
  }
  
  h1 {
    text-align: center;
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
    width: 100%;
  }
  
  .cards__items {
    flex-flow: row wrap;
    margin-bottom: 24px;
  }
  
  .cards__item {
    display: flex;
    flex: 29% 1;
    margin: 0 1rem;
    margin-bottom: 2rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
    cursor: pointer;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #eeeeee;
    background-color: #d65a31;
    box-sizing: border-box;
  }

  .cards__item__image {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 1000px rgba(34,40,49,0.2);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__link:hover {
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.3);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.03));
  }

