    transform: scale(1.1);
    box-shadow: inset 0 0 0 1000px rgba(34,40,49,0.0);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
